        padding: 0 3rem;
        line-height: 1.5rem;
        text-align: justify;
        overflow: hidden;
        width: 100%;
        display: -webkit-box;
        -webkit-line-clamp: 7;
        -webkit-box-orient: vertical;
        @include pad {
          -webkit-line-clamp: 5;
          padding: 0 2rem;
        }
      }
      @include md() {
        order: 1;
      }
      @include pad {
        height: 375px;
      }
    }
  }
}
.event03 {
  width: 100%;
  height: 700px;
  max-height: 100%;
  @include desktop {
    height: 500px;
  }
  @include md {
    height: 1000px;
  }
