<template>
  <div>
    <loading :active.sync="isLoading"></loading>
    <div class="text-right py-2">
      <button class="btn btn-primary" data-toggle="modal" @click="openModal(true)"></button>
    </div>
    <table class="table mt-4">
      <thead>
        <tr>
          <th width></th>
          <th></th>
          <th width>/</th>
          <th width></th>
          <th width></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item) in products" :key="item.id">
          <td>{{item.category}}</td>
          <td>{{item.title}}</td>
          <td>{{item.origin_price | currency }}/{{item.price | currency }}</td>
          <td>
            <span v-if="item.is_enabled" class="text-success"></span>
            <span v-else></span>
          </td>
          <td>
            <button class="btn btn-outline-primary btn-sm" @click="openModal(false,item)"></button>
            <button class="btn btn-outline-danger btn-sm" @click="removeData(item)"></button>
          </td>
        </tr>
      </tbody>
    </table>
    <!--  -->
    <Pagination :pagination="pagination" @emitPage="getProducts"></Pagination>
    <!-- / -->
    <!-- Modal -->
    <div
      class="modal fade"
      id="productModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog modal-lg" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-dark text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span v-if="isNew == true"></span>
              <span v-else></span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="row">
              <div class="col-sm-4">
                <div class="form-group">
                  <label for="image"></label>
                  <input
                    type="text"
                    class="form-control"
                    id="image"
                    v-model="tempProduct.imageUrl"
                    placeholder=""
                  >
                </div>
                <div class="form-group">
                  <label for="customFile">
                     
                    <i class="fas fa-spinner fa-pulse" v-if="status.fileUploading"></i>
                    <!--FA-->
                  </label>
                  <input
                    type="file"
                    id="customFile"
                    class="form-control"
                    ref="files"
                    @change="uploadFile"
                  >
                </div>
                <img
                  img="https://images.unsplash.com/photo-1483985988355-763728e1935b?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=828346ed697837ce808cae68d3ddc3cf&auto=format&fit=crop&w=1350&q=80"
                  :src="tempProduct.imageUrl"
                  class="img-fluid"
                  alt
                >
              </div>
              <div class="col-sm-8">
                <div class="form-group">
                  <label for="title"></label>
                  <input
                    type="text"
                    class="form-control"
                    id="title"
                    v-model="tempProduct.title"
                    placeholder=""
                  >
                </div>

                <div class="form-row">
                  <div class="form-group col-md-6">
                    <label for="category"></label>
                    <input
                      type="text"
                      class="form-control"
                      id="category"
                      v-model="tempProduct.category"
                      placeholder=""
                    >
                  </div>
                  <div class="form-group col-md-6">
                    <label for="price"></label>
                    <input
                      type="unit"
                      class="form-control"
                      id="unit"
                      v-model="tempProduct.unit"
                      placeholder=""
                    >
                  </div>
                </div>
                <div class="form-row">
                  <div class="form-group col-md-6">
                    <label for="origin_price"></label>
                    <input
                      type="number"
                      class="form-control"
                      id="origin_price"
                      v-model="tempProduct.origin_price"
                      placeholder=""
                    >
                  </div>
                  <div class="form-group col-md-6">
                    <label for="price"></label>
                    <input
                      type="number"
                      class="form-control"
                      id="price"
                      v-model="tempProduct.price"
                      placeholder=""
                    >
                  </div>
                </div>
                <hr>

                <div class="form-group">
                  <label for="description"></label>
                  <textarea
                    type="text"
                    class="form-control"
                    id="description"
                    v-model="tempProduct.description"
                    placeholder=""
                  ></textarea>
                </div>
                <div class="form-group">
                  <label for="content"></label>
                  <textarea
                    type="text"
                    class="form-control"
                    id="content"
                    v-model="tempProduct.content"
                    placeholder=""
                  ></textarea>
                </div>
                <div class="form-group row">
                  <div class="form-check col-6">
                    <input
                      class="form-check-input"
                      type="checkbox"
                      v-model="tempProduct.is_enabled"
                      :true-value="1"
                      :false-value="0"
                      id="is_enabled"
                    >
                    <label class="form-check-label" for="is_enabled"></label>
                  </div>
                  <div class="form-check col-6">
                    <input
                      class="form-check-input"
                      type="checkbox"
                      v-model="tempProduct.in_stock"
                      :true-value="1"
                      :false-value="0"
                      id="in_stock"
                    >
                    <label class="form-check-label" for="in_stock"></label>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
            <button type="button" class="btn btn-primary" @click="updateProduct"></button>
          </div>
        </div>
      </div>
    </div>
    <div
      class="modal fade"
      id="delProductModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-danger text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span></span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            
            <strong class="text-danger">{{ tempProduct.title }}</strong> ()
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
            <button type="button" class="btn btn-danger" @click="checkRemove"></button>
          </div>
        </div>
      </div>
    </div>
    <!-- Modal -->
  </div>
</template>
<script>
import $ from "jquery";
import Pagination from "../Pagination";

export default {
  components: {
    Pagination
  },
  data() {
    return {
      products: [],
      pagination: {}, //
      tempProduct: {
        in_stock:0,
      }, //
      isNew: false,
      isLoading: false,
      status: {
        fileUploading: false
      },
      modalTitle: ""
    };
  },
  methods: {
    getProducts(page = 1) {
      //api
      const api = `${process.env.APIPATH}/api/${
        process.env.CUSTOMPATH
      }/products?page=${page}`; //'https://vue-course-api.hexschool.io/api/apple/products';
      const vm = this;
      console.log(process.env.APIPATH, process.env.CUSTOMPATH);
      vm.isLoading = true; //
      this.$http.get(api).then(response => {
        console.log(response.data);
        vm.isLoading = false; //loading
        vm.products = response.data.products;
        vm.pagination = response.data.pagination;
      });
    },
    openModal(isNew, item) {
      //
      const vm = this;
      if (isNew) {
        this.tempProduct = {};
        this.isNew = true; //true=>
      } else {
        this.tempProduct = Object.assign({}, item); //object.assign
        this.isNew = false; //false=>
        console.log(item);
      }
      $("#productModal").modal("show");
    },
    removeData(item) {
      console.log(item);
      this.tempProduct = item;
      $("#delProductModal").modal("show");
    },
    checkRemove() {
      const vm = this;
      const api = `${process.env.APIPATH}/api/${
        process.env.CUSTOMPATH
      }/admin/product/${vm.tempProduct.id}`; //api
      this.$http.delete(api).then(response => {
        vm.products = response.data.products;
        $("#delProductModal").modal("hide");
        if (response.data.success) {
          console.log(response.data.success);
          vm.getProducts();
        } else {
          $("#delProductModal").modal("hide");
          vm.getProducts();
          console.log("");
        }
      });
    },
    updateProduct() {
      // 
      let api = `${process.env.APIPATH}/api/${
        process.env.CUSTOMPATH
      }/admin/product`;
      let httpMethod = "post";
      const vm = this;
      if (!vm.isNew) {
        //()
        api = `${process.env.APIPATH}/api/${
          process.env.CUSTOMPATH
        }/admin/product/${vm.tempProduct.id}`; //api
        httpMethod = "put"; //http
      }
      this.$http[httpMethod](api, { data: vm.tempProduct }).then(response => {
        console.log(response.data);
        if (response.data.success) {
          //
          $("#productModal").modal("hide"); //
          vm.getProducts(); //
        } else {
          //
          $("#productModal").modal("hide");
          vm.getProducts();
          console.log("");
        }
      });
    },
    uploadFile() {
      const uploadedFile = this.$refs.files.files[0]; //this
      const vm = this;
      const formData = new FormData(); //formDate
      formData.append("file-to-upload", uploadedFile); //append(,)
      const url = `${process.env.APIPATH}/api/${
        process.env.CUSTOMPATH
      }/admin/upload`; //
      vm.status.fileUploading = true; //fa
      this.$http
        .post(url, formData, {
          headers: {
            "Content-Type": "multipart/form-data" //form-data
          }
        })
        .then(response => {
          console.log(response.data);
          if (response.data.success) {
            //
            //set,(,'',)
            vm.$set(vm.tempProduct, "imageUrl", response.data.imageUrl);
            vm.status.fileUploading = false; //loading
          } else {
            //
            this.$bus.$emit("message:push", response.data.message, "danger");
          }
        });
    }
  },
  created() {
    //created
    this.getProducts();
    // this.$bus.$emit('message:push','','success');//
  }
};
</script>

<style lang="scss" scoped>
@import "@/assets/helpers/breakpoint.scss";
.modal-content {
  background:$color-bg;
}
</style>



